export default function sketch(s) {
  let scale1 = 1.2;
  let propfundo = 1;
  let scale2;
  let ix,iy;
  let num_fundoX,num_fundoY;
  let cwidth,cheight;
  let tls = [1,2,3,1];
  let prob = 20;
  let numColoridos = 3;
  let tfundosize = 4;
  let cVars = [[1.111,77,48],[0,0,33],[0,0,33]];
  

  const queryString = window.location.search;
  console.log("qstr:",queryString);
  const urlParams = new URLSearchParams(queryString);
  let parscale = urlParams.get('scale')
  let colorscheme = urlParams.get('colorscheme')
  parscale = Number(parscale)
  colorscheme = Number(colorscheme)
 // console.log("scale:",parscale, typeof parscale);

  if(parscale){
    scale1=parscale
  }

  if(colorscheme){
    if(colorscheme >=1 && colorscheme <= 2){
    let colors = [
        [[1.111,77,48],[0,0,33],[0,0,33]],
        [[57.5,100,40],[59.4,67,34],[0,0,33]]
    ]
    cVars = colors[colorscheme-1] 
    }
  }

  num_fundoX = 24;
  num_fundoY = 7;
  cwidth = 100*scale1*1/7*4*num_fundoX;
  cheight = 100*scale1*1/7*4*num_fundoY;
  
  scale2 = scale1/propfundo;
  
  s.preload = function(){ 
    console.log('preload')
    myfont  = s.loadFont('fonts/LibreFranklin-Regular.ttf')
    console.log('ok')
    console.log(dname)
  }
  

  s.setup = function(){ 
    s.createCanvas(cwidth, cheight, s.SVG);
    s.colorMode(s.HSL,100);
    let color1 = s.color(cVars[0][0],cVars[0][1],cVars[0][2])
    let color2 = s.color(cVars[1][0],cVars[1][1],cVars[1][2])
    let color3 = s.color(cVars[2][0],cVars[2][1],cVars[2][2])
    let tramaParams = [[0,[0,0],color2],[1,[0,0],color2],[2,[2,0],color1],[3,[5,0],color1]]
    let tramaletters = []
    let fundoletters = []
    

    s.background(1,0,100);
    ix = 3 + 1/7;//s.floor(0 + s.random(2));
    iy = 1 + 1/7 + 1/14;//s.floor(1 + s.random(1));
    
    // args: 
    //    sketch, 
    //    elemento a ser desenhado (numero)
    //    escala, 
    //    ponto x, 
    //    ponto y,
    //    pad x
    //    pad y
    //    rotao
    //    cor
    //    clip (px,py,pendx,pendy)
    for(let fx = 0; fx < num_fundoX; fx++){
      for(let fy = 0; fy < num_fundoY; fy++){
        let cond_XL = (fx>4);
        let cond_XR = (fx<19);
        let cond_YT = (fy>1);
        let cond_YB = (fy<4);
        let cond_comb = !(cond_XL && cond_XR && cond_YT && cond_YB);

        if(s.random(100) < prob){
          if(cond_comb)
            {
            let lnum,fletra, cor, rot,clipx,clipy,clipex,clipey;
    
            cor = s.color(cVars[2][0],cVars[2][1],cVars[2][2],10+s.floor(s.random(4)*5))
            rot = [0,1,2,3].choose();
            lnum = [0,1,2,3].choose();//[1,2,3].choose();//s.floor(s.random(2));

            clipx = s.floor(s.random(tls[lnum]))*7 - 1 + [0,7-tfundosize].choose();;
            
            clipy = s.floor([0,(7-tfundosize)].choose());
            clipex = clipx + tfundosize;
            clipey = clipy + tfundosize;
            fletra =  new TLetter(s,lnum,scale2,fx*4/7,fy*4/7,-clipx,-clipy ,rot,cor,[clipx,clipy,clipex,clipey])
            fundoletters.push(fletra)

          }
        }
      }
    }

console.log(fundoletters.length)
let randA = arithmSer(0,fundoletters.length-1).shuffle().firstN(numColoridos);

randA.forEach(function(i){
  fundoletters[i].cor = s.color(cVars[0][0]-2,cVars[0][1]+13,cVars[0][2]-12,80+s.floor(s.random(4)*5))
})

fundoletters.forEach(function(l){
  l.display()
})
  

for(let par of tramaParams){
  let lnum, lx, ly, cor , letra;

  lnum = par[0];
  lx = par[1][0];
  ly = par[1][1];
  cor = par[2]
  letra = new TLetter(s,lnum,scale1,lx+ix,ly+iy,0,0,0,cor)

  tramaletters.push(letra)
  letra.display();
}

s.noStroke(); 
s.fill(0,0,13);
s.textSize(100*scale1*0.3);
s.textFont(myfont);
s.text('Research',(ix+6+1/7)*scale1*100 , (iy+1)*scale1*100);

  }

 

}


