Array.prototype.choose = function () {
  return this[Math.floor((Math.random()*this.length))];
}

Array.prototype.shuffle = function () {
  let randarr;
  randarr = this.sort(() => Math.random() - 0.5);
  return randarr;
}

Array.prototype.firstN = function (n=1) {
  let arr = [];
  for(let i=0; i<n; i++){
    arr.push(this[i])
  }
  return arr;
}

function arithmSer(start, end) {
  let myArray = [];
  for (let i = start; i <= end; i++) {
    myArray.push(i);
  }
  return myArray;
}
